import { gameobjects as gameobjects_store, points as points_store } from '../stores/objects';
let gameobjects;
gameobjects_store.subscribe(v => gameobjects = v);
let points;
points_store.subscribe(v => points = v);
export var UndoType;
(function (UndoType) {
    UndoType[UndoType["Add"] = 0] = "Add";
    UndoType[UndoType["Remove"] = 1] = "Remove";
    UndoType[UndoType["Move"] = 2] = "Move";
})(UndoType || (UndoType = {}));
export class Undo {
    constructor(type, options) {
        this.type = type;
        this.options = options;
    }
}
function process_change(array) {
    const change = array == "undos" ? undos.pop() : redos.pop();
    if (!change)
        return;
    switch (change.type) {
        case UndoType.Add: {
            switch (change.options.array) {
                case "gameobjects": {
                    (array == "undos" ? redos : undos).push(new Undo(UndoType.Remove, {
                        array: "gameobjects",
                        key: change.options.key,
                        object: gameobjects[change.options.key]
                    }));
                    gameobjects.splice(change.options.key, 1);
                    gameobjects_store.set(gameobjects);
                    break;
                }
                case "points": {
                    (array == "undos" ? redos : undos).push(new Undo(UndoType.Remove, {
                        array: "points",
                        key: change.options.key,
                        object: points[change.options.key]
                    }));
                    points.splice(change.options.key, 1);
                    points_store.set(points);
                    break;
                }
            }
            break;
        }
        case UndoType.Remove: {
            switch (change.options.array) {
                case "gameobjects": {
                    (array == "undos" ? redos : undos).push(new Undo(UndoType.Add, {
                        array: "gameobjects",
                        key: change.options.key
                    }));
                    gameobjects.splice(change.options.key, 0, change.options.object);
                    gameobjects_store.set(gameobjects);
                    break;
                }
                case "points": {
                    (array == "undos" ? redos : undos).push(new Undo(UndoType.Add, {
                        array: "points",
                        key: change.options.key
                    }));
                    points.splice(change.options.key, 0, change.options.object);
                    points_store.set(points);
                    break;
                }
            }
            break;
        }
        case UndoType.Move: {
            switch (change.options.array) {
                case "gameobjects": {
                    (array == "undos" ? redos : undos).push(new Undo(UndoType.Move, Object.assign(Object.assign({}, change.options), { array: "gameobjects" })));
                    const object = gameobjects[change.options.key];
                    object.x = change.options.old_x;
                    object.y = change.options.old_y;
                    gameobjects[change.options.key] = object;
                    gameobjects_store.set(gameobjects);
                    break;
                }
                case "points": {
                    (array == "undos" ? redos : undos).push(new Undo(UndoType.Move, Object.assign(Object.assign({}, change.options), { array: "points" })));
                    const object = points[change.options.key];
                    object.x = change.options.old_x;
                    object.y = change.options.old_y;
                    points[change.options.key] = object;
                    points_store.set(points);
                    break;
                }
            }
            break;
        }
    }
}
export function undo() {
    process_change("undos");
}
export function redo() {
    process_change("redos");
}
export function add_undo(type, options) {
    undos.push(new Undo(type, options));
    redos.length = 0;
}
const undos = new Array();
const redos = new Array();
